<template>
  <div class="backColor">
    <mu-appbar style="width: 100%;" :color="$store.state.color" class="AssistantHeader">
      <div slot="left" class="icon" size="20" @click="()=>{$router.go(-1)}">
        <mu-button icon slot="left" class="icon" size="20">
          <mu-icon value="keyboard_arrow_left"></mu-icon>
        </mu-button>
      </div>
      <div style="width:100%;text-align:center;" class="positionRelative">
        <span style="font-size:18px;color:white;">{{normal.value1}}</span>
      </div>
      <div slot="right" class="icon" size="20" @click="openBotttomSheet">
        <mu-button icon slot="left" class="icon" size="20">
          <img src="static/img/queryNews.png" alt srcset />
        </mu-button>
      </div>
    </mu-appbar>
    <div class="contanerMain">
      <div style="background:white;margin-top:10px;" class="border-radius">
        <van-swipe indicator-color="orange">
          <van-swipe-item>
            <div id="mainpH" class="echartHis"></div>
          </van-swipe-item>
          <!-- maincod -->
          <van-swipe-item>
            <div id="maincod" class="echartHis"></div>
          </van-swipe-item>
          <van-swipe-item>
            <div id="mainammoniaNitrogen" class="echartHis"></div>
          </van-swipe-item>
          <van-swipe-item>
            <div id="mainchromium" class="echartHis"></div>
          </van-swipe-item>
          <van-swipe-item>
            <div id="maincuprum" class="echartHis"></div>
          </van-swipe-item>
          <van-swipe-item>
            <div id="mainnickel" class="echartHis"></div>
          </van-swipe-item>
          <van-swipe-item>
            <div id="mainnitrogen" class="echartHis"></div>
          </van-swipe-item>
          <van-swipe-item>
            <div id="mainphosphorus" class="echartHis"></div>
          </van-swipe-item>
          <van-swipe-item>
            <div id="mainprussiate" class="echartHis"></div>
          </van-swipe-item>
        </van-swipe>
      </div>
    </div>
    <div class="tableContenthistory">
      <div class="TimeDataTableContent">
        <myTableData
          ref="mychild"
          :waterstation="normal.value1"
          :timeDate1="value3"
          :timeDate2="value4"
        />
      </div>
    </div>

    <mu-container>
      <mu-bottom-sheet :open.sync="open">
        <mu-list @item-click="closeBottomSheet">
          <mu-sub-header>筛选</mu-sub-header>
          <mu-list-item>
            <div class="titleselect">
              <span>站点名称：</span>
            </div>
            <div class="select" @click="selectDataPicoptions" id="classPicoptions">
              <input type="text" placeholder="请选择站点" class="input" disabled v-model="normal.value1" />
            </div>
          </mu-list-item>
          <mu-list-item>
            <div class="titleselect">
              <span>开始时间：</span>
            </div>
            <div class="select" @click="selectStartTime('start')">
              <input type="text" class="input" disabled v-model="value3" />
            </div>
          </mu-list-item>
          <mu-list-item>
            <div class="titleselect">
              <span>结束时间：</span>
            </div>
            <div class="select" @click="selectLastTime('end')">
              <input type="text" class="input" disabled v-model="value4" />
            </div>
          </mu-list-item>
          <mu-list-item>
            <div class="btnBottom">
              <div class="left" @click="closeBottomSheet()">取消</div>
              <div class="right" :style="'background:'+$store.state.color" @click="Confirm()">确定</div>
            </div>
          </mu-list-item>
        </mu-list>
      </mu-bottom-sheet>
    </mu-container>

    <mu-container>
      <mu-bottom-sheet :open.sync="openTime">
        <mu-list @item-click="closeBottomSheet">
          <mu-sub-header>时间筛选</mu-sub-header>
          <van-datetime-picker
            v-model="currentDate"
            type="date"
            :min-date="minDate"
            @confirm="ConfirmTime"
            @cancel="cancel"
          />
        </mu-list>
      </mu-bottom-sheet>
    </mu-container>
  </div>
</template>
<script>
var echarts = require("echarts");
import HisEcharts from "../../../static/js/History/index";
import AppBace from "../../../static/ajax";
import myTableData from "../event/historyDataD";
import EchartsCommon from "../../../static/js/echartsCommon";
import Vue from "vue";
import { Toast } from "vant";
Vue.use(Toast);
export default {
  name: "Assistant",
  data() {
    return {
      imageListCasoul: [1, 2, 3, 4],
      status1: true,
      status2: true,
      msg: "",
      options: ["沙井一期进水口水质监测站"],
      value3: AppBace.filter(
        new Date(new Date(new Date().getTime() - 30 * 24 * 3600 * 1000))
      ),
      value4: AppBace.filter(new Date()),
      normal: {
        value1: this.$store.state.menu[0]
      },
      checkbox: {
        value1: [],
        value2: false,
        value3: false
      },
      checkboxContent: [
        { id: 1, content: "标液标准" },
        { id: 2, content: "标准曲线" },
        { id: 3, content: "精密度检查" }
      ],
      checkAll: true,
      count: 0,
      res: [],
      token: "",
      open: false,
      openTime: false,
      currentDate: new Date(),
      minDate: new Date("1970-07-01"),
      showTime: ""
    };
  },
  methods: {
    Confirm() {
      this.open = false;
      setTimeout(() => {
        this.initData();
      }, 50);
      this.$refs.mychild.initDataTable(this.value3,this.value4,this.normal.value1);
    },
    ConfirmTime(value) {
      this.openTime = false;
      switch (this.showTime) {
        case "start":
          this.value3 = AppBace.filter(value);
          break;
        case "end":
          this.value4 = AppBace.filter(value);
          break;
      }
    },
    cancel() {
      this.openTime = false;
    },
    selectStartTime(e) {
      this.showTime = e;
      this.openTime = true;
    },
    selectLastTime(e) {
      this.showTime = e;
      this.openTime = true;
    },
    selectDataPicoptions() {
      this.open = false;
      var _this = this;
      var pickerView = new PickerView({
        bindElem: classPicoptions, // 绑定的元素,用于区别多个组件存在时回显区别，如果单个可以随意填某个元素
        data: this.$store.state.menu, // 说明：该参数必须符合json格式 且最里层是个数组，如上面的data变量所展示的[3,4]。
        title: "选择站点", // 顶部标题文本 默认为“标题”
        leftText: "取消", // 头部左侧按钮文本 默认为‘取消’
        rightText: "确定", // 头部右侧按钮文本 默认为“确定”
        rightFn: function(selectArr) {
          // 点击头部右侧按钮的回调函数，参数为一个数组，数组对应滚轮中每项对应的值
          console.log(selectArr[0]);
          _this.normal.value1 = selectArr[0];
          _this.open = true;
        }
      });
    },
    toBack() {},
    openBotttomSheet() {
      this.open = true;
    },
    closeBottomSheet() {
      this.open = false;
    },
    async initData() {
      var advice = "";
      var day = 0;
      var url = "/station/water-data/form";
      //  参数：starttime/waterstation/lasttime
      var waterstation = this.normal.value1;
      var starttime = AppBace.filter(this.value3);
      var lasttime = AppBace.filter(this.value4);
      var isExcel = "no";
      day = AppBace.dateTimeDev(starttime, lasttime);
      var res = await AppBace.request(
        url,
        "POST",
        {
          starttime,
          lasttime,
          waterstation,
          isExcel
        },
        this.token
      );
      this.res = res;
      setTimeout(() => {
        HisEcharts.initHistoryDataEchartsPH(res.data.data);
      }, 50);
      // this.initTest(arraypH, arrayCu, arraygmtMonitor);
      // this.resData();
      this.$refs.mychild.initDataTable(this.value3,this.value4,this.normal.value1);
      if (day === 0) {
        day = "24小时";
      } else if (day < 0) {
        Toast("时间筛选有误，请重新选择");
        return;
      } else {
        day = day + "天";
      }
      if (res.data.data.length === 0) {
        advice = ",当前水站信息数据为空，请调整区间或者查看其他站点";
      }
      Toast("数据为最近" + day + "的历史数据" + advice);
    }
  },
  async mounted() {
    this.token = sessionStorage.getItem("token");
    this.options = this.$store.state.menu;
    await this.initData();
    this.status2 = false;
  },
  components: {
    myTableData
  }
};
</script>
<style scoped>
@import "../../../static/css/model.css";
.imageFlex {
  width: 100%;
  position: absolute;
  top: 40%;

  display: flex;
  flex-direction: row;
  /* justify-content: center; */
  justify-content: space-between;
}
.borderTime {
  display: flex;
  height: 40px;
  /* justify-content: center; */
  align-items: center;
  border: 1px solid #ccc;
  border-radius: 10px;
  padding: 0 5px;
  margin-top: 5px;
}
.contanerMain {
  margin: 0 10px;
  padding: 10px;
  background: white;
  margin-top: 60px;
  margin-bottom: 10px;
  border-radius: 5px;
  background: white;
  /* margin-bottom: 50px; */
}
.flexCenterMain {
  display: flex;
  height: 40px;
  /* justify-content: center; */
  align-items: center;
  border: 1px solid #ccc;
  border-radius: 10px;
  padding: 0 5px;
}
.flexCenterMainChild {
  display: flex;
  height: 33px;
  align-items: center;
  font-size: 14px;
  /* border-bottom: 1px #f1eff2 solid; */
}
.backColor {
  background: rgb(230, 228, 228);
  padding-top: 10px;
  padding-bottom: 10px;
}
.assContentStyles {
  background: white;
  padding: 10px;
  margin: 10px;
  border-radius: 5px;
}
.trendEcharts {
  width: 100%;
  height: 300px;
}
#mainPh {
  width: 100%;
  height: 150px;
}
#main {
  width: 100%;
  height: 300px;
}
#mainTest {
  width: 100%;
  height: 300px;
}
.AssistantHeader {
  position: fixed;
  top: 0;
  display: flex;
  flex-direction: row;
}
.imageListCasoul {
  width: 30px;
  text-align: center;
}
.imageListCasoul img {
  width: 16px;
  height: 16px;
  position: absolute;
  bottom: 0;
  /* left: 40px; */
}
#Main-Content {
  position: relative;
}
.tableContenthistory {
  background: white;
  margin: 0 10px;
  border-radius: 5px;
  padding: 5px;
  margin-bottom: 70px;
}
.tableContenthistoryTitle {
  display: flex;
  flex-direction: column;
}
.tableContenthistoryTitle span {
  height: 40px;
  display: flex;
  width: 60px;
  /* align-content: center; */
  justify-content: center;
  align-items: center;
}
.imageSizeStyles img {
  width: 20px;
  height: 20px;
  /* color: aliceblue; */
}
.btnStyles {
  border: none;
  background: #4ed995;
  border: #4ed995 solid 1px;
  border-radius: 5px;
  color: white;
  margin-top: 5px;
  outline: none;
}
.headerQuery {
  padding: 5px 0;
  border-bottom: 5px solid rgb(240, 239, 239);
}
.icon img {
  width: 24px;
  height: 24px;
}
#mainpH,
.echartHis {
  width: 100%;
  height: 300px;
}
</style>



